$background-animation-time: 2s;
$slide-hiding-time: 0.2s;
$slide-hiding-delay: 2.5s;

.first-slide {
  animation: disappearing $slide-hiding-time ease-in-out $slide-hiding-delay;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}
@keyframes disappearing {
  to {
    left: -100%;
    opacity: 0.3;
  }
}

.first-slide__background {
  position: absolute;
  top: -27%;
  left: -73%;
  z-index: -1;
  overflow-y: hidden;

  width: 245%;
  height: 122%;
  background: linear-gradient(#4D43FF, #9B4AFF 100%);
  border-radius: 50%;
  transform: scale(3);
  animation: background-appearance $background-animation-time ease-in-out;
}
@keyframes background-appearance {
  from {
    transform: translateY(-96%) scale(1);
  }
  10% {
    transform: translateY(-96%) scale(1);
  }
  40% {
    transform: translateY(-27%) scale(1);
  }
  50% {
    transform: translateY(-27%) scale(1);
  }
  to {
    transform: translateY(0) scale(3);
  }
}

.first-slide__title {
  @include text-styles(36px, 500);
  width: 70%;
  margin: auto;
  padding-top: 30%;
}

.first-slide__hint {
  position: absolute;
  bottom: 80px;

  @include text-styles(29px, 600);
  width: 100%;
  margin: auto;
  padding-top: 30%;
}
