@import 'fonts.scss';
@import 'mixins.scss';

$window-height: 100vh;

html {
  overflow: hidden;
}
body {
  margin: 0;
  overflow-y: hidden;
  box-sizing: border-box;
}

.main-container {
  width: 100%;
  height: $window-height;
  overflow-y: hidden;
}

.slide {
  height: 100vh;
  overflow: hidden;
}

@import 'slides/first-slide.scss';
@import 'slides/second-slide.scss';
@import 'slides/third-slide.scss';
@import 'slides/fourth-slide.scss';
@import 'slides/fifth-slide.scss';
