$third-slide-top-line-animation-time: .3s;
$third-slide-middle-line-animation-time: .7s;
$third-slide-bottom-line-animation-time: 1.2s;

.third-slide {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  animation: slide-disappearance .7s ease-in-out 2.4s;
}

.third-slide__line-top {
  width: 100%;
  position: absolute;
  top: 0;
  z-index: 5;
}
.third-slide__blind-top {
  position: absolute;
  right: 0;
  top: 0;
  z-index: 6;
  width: 0;
  height: 33vh;
  background-color: #fff;
  animation: open-blind-top $third-slide-top-line-animation-time ease-out;
}
@keyframes open-blind-top {
  from {
    width: 100%;
  }
  to {
    width: 0;
  }
}
.third-slide__text-top {
  position: absolute;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  box-sizing: border-box;
  opacity: 0.5;

  @include text-styles(36px, 700);
}

.third-slide__line-middle {
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  z-index: 3;
}
.third-slide__blind-middle {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 4;
  width: 0;
  height: 100vh;
  background-color: #fff;
  animation: open-blind-middle $third-slide-middle-line-animation-time ease-out;
}
@keyframes open-blind-middle {
  from {
    width: 100%;
  }
  45% {
    width: 100%;
  }
  to {
    width: 0;
  }
}
.third-slide__text-middle {
  position: absolute;
  z-index: 10;
  top: 35vh;
  left: 0;
  width: 100%;
  box-sizing: border-box;

  @include text-styles(36px, 600);
  @include text-animation(1.5s);
}

.third-slide__line-bottom {
  width: 100%;
  position: absolute;
  bottom: 0;
  z-index: 1;
}
.third-slide__blind-bottom {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 2;
  width: 0;
  height: 33vh;
  background-color: #fff;
  animation: open-blind-bottom $third-slide-bottom-line-animation-time ease-out;
}
@keyframes open-blind-bottom {
  from {
    width: 100%;
  }
  30% {
    width: 100%;
  }
  to {
    width: 0;
  }
}
.third-slide__text-bottom {
  position: absolute;
  z-index: 10;
  bottom: 0;
  left: -20px;
  width: 100%;
  margin: 0;
  box-sizing: border-box;

  @include text-styles(2000%, 700);
  text-align: left;
  line-height: 65%;
  color: rgba(255,255,255,0.5);
  @include text-animation(2.1s);
}
