.fifth-slide {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: linear-gradient(#4D43FF, #9B4AFF 100%);
  animation: fifth-slide-appearance .5s ease-in-out;
}
@keyframes fifth-slide-appearance {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.fifth-slide__text-top {
  position: absolute;
  z-index: 10;
  top: 8vh;
  left: 0;
  width: 100%;
  padding: 0 60px;
  box-sizing: border-box;

  @include text-styles(36px, 600);
  @include text-animation(1.1s);
}

.fifth-slide__text-bottom {
  position: absolute;
  z-index: 10;
  bottom: 8vh;
  left: 0;
  width: 100%;
  padding: 0 60px;
  box-sizing: border-box;

  @include text-styles(36px, 600);
  @include text-animation(1.1s);
}
